@import 'mixins';
@import 'variables';

.details {
  color: $colorWhite;

  @media (min-width: 320px) {
    &__position {
      @include grid;
    }

    &__h2 {
      font-weight: 500;
      font-size: 32px;
      line-height: 32px;
      letter-spacing: -1.5px;
      text-align: center;
      padding-top: 92px;
    }

    &__a1,
    &__a3,
    &__a6 {
      grid-column: 1 span;
    }

    &__a2,
    &__a4,
    &__a5 {
      grid-column: 1 span;
    }

    &__img1 {
      display: flex;
      transition: transform 0.3s ease;
      height: 132px;
      width: 100%;
      margin-top: 48px;
      margin-bottom: 16px;
      transition: transform 0.4s;

      &:hover {
        transform: scale(1.05);
      }
    }

    &__img2 {
      display: flex;
      transition: transform 0.3s ease;
      width: 100%;
      height: 130px;
      margin-top: 48px;
      margin-bottom: 16px;
      grid-column: span 12;
      transition: transform 0.4s;

      &:hover {
        transform: scale(1.05);
      }
    }

    &__img3 {
      display: flex;
      transition: transform 0.3s ease;
      height: 130px;
      width: 100%;
      margin-top: 48px;
      margin-bottom: 16px;
      transition: transform 0.4s;

      &:hover {
        transform: scale(1.05);
      }
    }

    &__img4 {
      display: flex;
      transition: transform 0.3s ease;
      height: 130px;
      width: 100%;
      margin-top: 48px;
      margin-bottom: 16px;
      transition: transform 0.4s;

      &:hover {
        transform: scale(1.05);
      }
    }

    &__img5 {
      display: flex;
      transition: transform 0.3s ease;
      height: 130px;
      width: 100%;
      margin-top: 48px;
      margin-bottom: 16px;
      transition: transform 0.4s;

      &:hover {
        transform: scale(1.05);
      }
    }

    &__img6 {
      display: flex;
      transition: transform 0.3s ease;
      height: 130px;
      width: 100%;
      margin-top: 48px;
      margin-bottom: 16px;
      transition: transform 0.4s;

      &:hover {
        transform: scale(1.05);
      }
    }

    &__bike {
      font-weight: 700;
      font-size: 24px;
      line-height: 33.6px;
      padding-top: 16px;
      padding-bottom: 8px;

      &-txt {
        font-weight: 400;
        font-size: 16px;
        line-height: 22.4px;
      }
    }

    &__button {
      display: block;
      color: $bgColor;
      text-decoration: none;
      background-color: $colorWhite;
      width: 278px;
      height: 56px;
      border-radius: 40px;
      margin-top: 40px;
      margin-bottom: 80px;
      padding: 14.5 108.5px;
      font-weight: 700;
      font-size: 16px;
      line-height: 27px;

      &:hover {
        box-shadow: 0 0 10px rgba(0, 123, 255, 0.7);
      }
    }
  }

  @media (min-width: 576px) {
    &__position {
