      width: 392px;
      height: 56px;
      border-radius: 40px;
      margin-top: 48px;
      padding: 14.5 165.5px;
      font-weight: 700;
      font-size: 16px;
      line-height: 27px;

      &:hover {
        box-shadow: 0 0 10px rgba(0, 123, 255, 0.7);
      }
    }
  }

