    &__button {
      display: block;
      color: $bgColor;
      text-decoration: none;
      background-color: $colorWhite;
      width: 324px;
      height: 56px;
      border-radius: 40px;
      margin: 0 auto;
      margin-top: 48px;
      padding: 14.5 131.5px;
      font-weight: 700;
      font-size: 16px;
      line-height: 27px;

      &:hover {
        box-shadow: 0 0 10px rgba(0, 123, 255, 0.7);
      }
    }
  }
