.footer {
  overflow-x: auto;
  z-index: -1;

  @media (min-width: 320px) {
    & {
      max-width: 100%;
      background-image: url(/src/images/footer1.png);
      height: 200px;
      background-position: center;
      background-size: cover;
    }
  }

  @media (min-width: 576px) {
    & {
      max-width: 100%;
      height: 480px;
      background-image: url(/src/images/footer2.png);
      background-position: center;
      background-size: cover;
    }
  }

  //замінити н6а background-image
  @media (min-width: 1200px) {
    & {
      max-width: 100%;
      background-image: url(/src/images/last-image.png);
      max-height: 555px;
      background-position: top;
      background-size: cover;
    }
  }
}
